export default {
  props: {
    title: {
      type: String,
      required: true
    },
    value: {
      type: [Number, Array],
      required: true
    },
    max: {
      type: Number,
      required: true
    },
    step: {
      type: Number,
      required: true
    }
  },

  data () {
    return {
      isDisabled: false
    }
  },

  created () {
    this.checkDisabled()
  },

  methods: {

    checkDisabled () {
      if (Array.isArray(this.value)) {
        this.isDisabled = true
      } else {
        this.isDisabled = false
      }
    },

    passValue (payload) {
      this.$emit('change', payload)
    },

    passEvent (payload) {
      console.log('switch', this.isDisabled)
      this.$emit('input', this.isDisabled)
    }

  }
}

